  _plGetWindowWidth,
  _plGetCurrentBreakpoint,
  _plGetElCount,
  _plIsInViewport,
  _plGetTopOfViewPort,
  _plGetOffsetTop,
  _plCreateElementFromHTML,
  _plGetHeightOfElement,
  _plIsElement,
  _plCreateInnerWrap,
  _plInsertAfter,
  _plElementClosest,
